//////   //////

//  

.section-header {
  width: 100%;
  background-color: $bg-color-green;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: 20;
}

//  

.header {
  display: flex;
  align-items: center;
  padding-top: 12px;

  @media screen and (min-width: 768px) {
    padding-top: 20px;
    padding-left: 16px;
    padding-right: 16px;
  }

  @media screen and (min-width: 1200px) {
    padding-top: 8px;
    padding-right: 0;
    padding-left: 0;
  }
}

//   

.header__logo {
  @include minW-Dt {
    margin-right: 308px;
  }
}

.header__logo--svg {
  width: 40px;
  height: 37px;

  @include minW-Tablet {
    width: 48px;
    height: 44px;
  }

  @include minW-Dt {
    width: 60px;
    height: 65px;
  }
}

//     

.menu-toggle {
  display: flex;
  align-items: center;
  padding: 0;
  border: 0;
  background-color: transparent;
  color: #ffffff;

  margin-left: auto;

  @media screen and (min-width: 1200px) {
    display: none;
  }
}

//      

.toggle-icon {
  fill: currentColor;
}

//    

.header__navigation {
  @media screen and (max-width: 1199px) {
    display: none;
  }
}

.nav-menu__list {
  display: flex;
}

.nav-menu__item:not(:last-child) {
  margin-right: 32px;
}

.nav-menu__link {
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: calc(20 / 16);
  color: #fcfcfc;
  transition: color $transition;

  &:hover {
    color: #f8d24e;
  }
}

//  
.button__text {
  font-weight: 600;
  font-size: 14px;
  line-height: 1.2142;

  margin-right: 4px;
}
.button__text:focus {
  color: $white;
}
.header__button {
  @media screen and (max-width: 767px) {
    display: none;
  }

  display: flex;
  align-items: center;
  justify-content: center;
  color: #131313;

  margin-left: 32px;

  @media screen and (min-width: 1200px) {
    margin-left: auto;
  }

  background: #f8d24e;
  border-radius: 10px;

  min-width: 152px;
  min-height: 42px;

  border: none;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    background: $accent-color;
  }

  &:focus {
    background: $accent-color;
    color: #ffffff;
  }
}

.header__button-svg {
  fill: currentColor;
}

/* ----------------------------------  ---------------------------------- */
.menu {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: $bg-color-green;
}
.menu__container {
  position: relative;
  overflow: auto;
  height: 100%;
  max-width: 768px;
  width: 100%;
  padding: 0 32px;
  @include minW-Tablet {
    max-width: 1200px;
    width: 100%;
  }
}
//   

.menu__logo {
  position: absolute;
  top: 12px;
  left: 32px;

  @include minW-Tablet {
    top: 20px;
  }
}

.menu__logo--svg {
  width: 40px;
  height: 37px;

  @include minW-Tablet {
    width: 48px;
    height: 44px;
  }
}

//    

.menu__buttoncls {
  display: flex;
  position: absolute;
  padding: 0;
  right: 16px;
  top: 20px;
  color: #131313;

  @media screen and (min-width: 768px) {
    right: 32px;
    top: 32px;
  }

  background-color: transparent;
  border: none;
}

//  

.menu__list {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-basis: calc((100% - 96px) / 4);
  gap: 32px;
  margin-top: 100px;

  @media screen and (min-width: 768px) {
    margin-top: 140px;
    gap: 40px;
    flex-basis: calc((100% - 120px) / 4);
  }
}

//   

.menu__link {
  font-style: normal;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.2187;
  color: #fcfcfc;
  &:hover {
    color: #131313;
  }
  @include minW-Tablet {
    font-size: 48px;
    line-height: 1.2291;
  }
}
